import sassTopBar from './index.module.scss'
import {LeftOutlined,EnvironmentOutlined,SearchOutlined} from '@ant-design/icons'
import {  Input, Select, Space } from 'antd';
import {useNavigate} from 'react-router-dom'
import React from 'react';
import {useSelector} from "react-redux";


export default function TopBar(props){

    const {label,pinyin,short,value} = useSelector((state)=>state.commodity)
    const navigate =  useNavigate()
    let showBackIcon = null
    function backView(){
        navigate(-1)
    }
    function toCity(){
        navigate('/city')
    }
    if(props.showBack){
        showBackIcon = <div onClick={backView} className={sassTopBar.backIcon}><LeftOutlined style={{fontSize:'22px'}}/></div>
    }
    return (
        <div className={sassTopBar.topBarT} style={{margin:props.showBack?'0 0':'10px 0'}}>
            {showBackIcon}
            <div className={sassTopBar.searInput} style={{marginLeft:props.showBack?'0px':'25px',backgroundColor:'white'}}>
                <Space.Compact size='large'>
                    <Select defaultValue={label} open={false} bordered={false} onClick={toCity}/>
                    <Input
                    prefix={<SearchOutlined style={{fontSize:'18px',color:'#0c0c0c'}}/>}
                    placeholder="请输入地址" bordered={false}/>
                </Space.Compact>
            </div>
            <div className={sassTopBar.topMap}>
                <EnvironmentOutlined 
                onClick={()=>{
                    navigate('/map')
                }}
                style={{fontSize:'22px',color:props.showBack?'black':'#fbfbfb'}}/>
            </div>
        </div>
    )
}