import TopBar from "../../component/topBar"
import sassHome from './index.module.scss'
import React, { useEffect } from 'react';
import { Carousel } from 'antd';
import { getBanner,getGroups,getNews } from "../../api/home";
import {HomeOutlined,UsergroupAddOutlined,GlobalOutlined,KeyOutlined} from '@ant-design/icons'


export default function Home(){
    let [bannerImgs,setBannerImgs] = React.useState([])
    let [groupsArr,setGroupsArr] = React.useState([])
    let [news,setNews] = React.useState([])
    let optionCar = [
        {text:'整租',icon:<HomeOutlined />},
        {text:'合租',icon:<UsergroupAddOutlined />},
        {text:'地图找房',icon:<GlobalOutlined />},
        {text:'去出租',icon:<KeyOutlined />},
    ]
    const contentStyle = {
        width:'100%',
        lineHeight: '0',
    };
    useEffect(()=>{
        getBanner().then(res=>{
            setBannerImgs(res.body)
        }) 
        getGroups().then(res=>{
            setGroupsArr(res.body)
        })
        getNews().then(res=>{
            setNews(res.body)
        })
    },[])
    let bannerS =   bannerImgs.map((item,index)=>{
        return  <div key={index}>
                    <div style={contentStyle}>
                        <img style={contentStyle} src={'http://8.129.190.90:8080'+item.imgSrc} alt={item.alt}/>
                    </div>
                </div>
    })
    let groups = groupsArr.map((item,index)=>{
        return  <div key={item.id}>
                    <div>
                        <p>{item.title}</p>
                        <p>{item.desc}</p>
                    </div>
                    <div>
                        <img src={'http://8.129.190.90:8080'+item.imgSrc} alt=''/>
                    </div>
                </div>
    })
    let newItem = news.map((item,index)=>{
        return  <div key={item.id}>
                    <div>
                        <img src={'http://8.129.190.90:8080'+item.imgSrc}></img>
                    </div>
                    <div>
                        <p>{item.title}</p>
                        <div>
                            <p>{item.from}</p>
                            <p>{item.date}</p>
                        </div>
                    </div>
                </div>
    })

    return (
        <div className={sassHome.Home}>
            <div className={sassHome.banner}>
                <div className={sassHome.searBox}>
                    <TopBar showBack={false}/>
                </div>
                <Carousel autoplay>
                   {bannerS}
                </Carousel>
            </div>
            <div className={sassHome.optionCar}>
                {optionCar.map(item=>{
                    return <div key={item.text}>
                        {item.icon}
                        <p>{item.text}</p>
                    </div>
                })}
            </div>
            <div className={sassHome.groups}>
                <div>
                    <p>租房小组</p>
                    <span>更多</span>
                </div>
                <div className={sassHome.groupsItem}>
                    {groups}
                </div>
            </div>
            <div className={sassHome.groupsNew}>
                <div className={sassHome.newTop}>
                    最新资讯
                </div>
                <div>{newItem}</div>
            </div>
        </div>
    )
}