import server from "./server";

export function getBanner(){
    return server({
        url:'/home/swiper',
    })
}

export function getGroups(){
    return server({
        url:'/home/groups'
    })
}

export function getNews(){
    return server({
        url:'/home/news'
    })
}

export function getCitys(){
    return server({
        url:'/area/city',
        params:{
            level:1,
        }
    })
}

export function getHot(){
    return server({
        url:'/area/hot',
    })
}